<template>
  <div class="calendar-container">
    <!-- Í≥†Ï†ï Ìó§Îçî ÏòÅÏó≠ -->
    <div class="fixed-header">
      <div class="header-top">
        <h1 class="month-title">{{ currentMonthTitle }}</h1>
        <div class="header-bottom">
          <label for="startDay">ÏãúÏûë ÏöîÏùº:</label>
          <select id="startDay" v-model="startDayIndex">
            <option value="0">ÏùºÏöîÏùº</option>
            <option value="1">ÏõîÏöîÏùº</option>
          </select>
        </div>
      </div>
    </div>

    <!-- ÏöîÏùº ÌëúÏãú ÏòÅÏó≠ -->
    <div class="weekdays">
      <span
        v-for="day in reorderedWeekdays"
        :key="day"
        :class="{'sunday': day === 'Ïùº', 'saturday': day === 'ÌÜ†'}"
      >
        {{ day }}
      </span>
    </div>

    <!-- ÎÇ†Ïßú ÏÖÄ ÏòÅÏó≠ -->
    <div class="days">
      <span
        v-for="day in daysInCurrentMonth"
        :key="day.key"
        :class="{
          'day': true,
          'padded': day.padded,
          'today': day.isToday,
          'my-duty': getDutyName(day.number) && getDutyName(day.number).isMe
        }"
        @click="handleDayClick(day)"
      >
        {{ day.padded ? '' : day.number }}
        <div v-if="getDutyName(day.number)" class="duty-name">
          {{ getDutyName(day.number).name }}
        </div>
      </span>
    </div>
    <p>hihi</p>

    <!-- Ïõî Ïù¥Îèô Î≤ÑÌäº -->
    <div class="nav-buttons-container">
      <button @click="prevMonth" class="nav-button prev-button">‚Äπ</button>
      <button @click="nextMonth" class="nav-button next-button">‚Ä∫</button>
    </div>

    <!-- PWA ÏóÖÎç∞Ïù¥Ìä∏ ÏïåÎ¶º -->
    <div v-if="updateExists" class="update-notification">
      <p>ÏÉàÎ°úÏö¥ Î≤ÑÏ†Ñ Î∞úÍ≤¨! ÏóÖÎç∞Ïù¥Ìä∏ÌïòÏãúÍ≤†ÏäµÎãàÍπå?</p>
      <button @click="refreshApp">ÏóÖÎç∞Ïù¥Ìä∏</button>
    </div>

    <!-- ÎπÑÎ∞ÄÎ≤àÌò∏ ÏûÖÎ†• Î™®Îã¨ -->
    <div v-if="showPasswordModal" class="modal-overlay">
      <div class="modal-content">
        <h3>ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏</h3>
        <p>ÎãπÏßÅ Ï†ïÎ≥¥Î•º ÏàòÏ†ï/ÏûÖÎ†•ÌïòÎ†§Î©¥ ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî.</p>
        <input type="password" v-model="passwordInput" @keyup.enter="validatePassword" placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏" autofocus>
        <div class="modal-buttons">
          <button @click="validatePassword" class="modal-confirm">ÌôïÏù∏</button>
          <button @click="cancelPassword" class="modal-cancel">Ï∑®ÏÜå</button>
        </div>
        <p v-if="passwordMessage" class="error-message">{{ passwordMessage }}</p>
      </div>
    </div>
    
    <!-- ÎãπÏßÅ Ï†ïÎ≥¥ ÏûÖÎ†•/ÏàòÏ†ï Ìèº -->
    <div v-if="showEditForm" class="edit-form-container">
      <h2>{{ formDocId ? 'ÎãπÏßÅ Ï†ïÎ≥¥ ÏàòÏ†ï' : 'ÏÉà ÎãπÏßÅ Ï†ïÎ≥¥ ÏûÖÎ†•' }} ({{ currentEditingDate }})</h2>
      
      <div class="form-grid">
        <label for="category">Category:</label>
        <input id="category" type="text" v-model="formEntry.category">

        <label for="holidayYn">Holiday (Y/N):</label>
        <select id="holidayYn" v-model="formEntry.holidayYn">
          <option value="Y">Y</option>
          <option value="N">N</option>
        </select>
        
        <label for="meYn">Me (Y/N):</label>
        <select id="meYn" v-model="formEntry.meYn">
          <option value="Y">Y</option>
          <option value="N">N</option>
        </select>
        
        <label for="name">Name:</label>
        <input id="name" type="text" v-model="formEntry.name">
        
        <div class="form-buttons">
          <button @click="saveDuty" class="save-button">Ï†ÄÏû•</button>
          <button @click="closeForm" class="cancel-button">Ï∑®ÏÜå</button>
        </div>
      </div>
      <p v-if="formMessage" :class="{'error-message': formError, 'success-message': !formError}">
        {{ formMessage }}
      </p>
    </div>

  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    dutyData: {
      type: Array,
      default: () => []
    },
    // App.vueÏóêÏÑú Ï†ÑÎã¨Î∞õÏùÄ Firestore Ìï®Ïàò
    saveDutyEntry: {
      type: Function,
      required: true
    },
    checkPassword: {
      type: Function,
      required: true
    }
  },
  data() {
    return {
      weekdays: ['Ïùº', 'Ïõî', 'Ìôî', 'Ïàò', 'Î™©', 'Í∏à', 'ÌÜ†'],
      currentDate: new Date(),
      startDayIndex: parseInt(localStorage.getItem('startDayIndex')) || 0,
      updateExists: false,
      registration: null,

      // -- Ï∂îÍ∞ÄÎêú ÏÉÅÌÉú --
      lastTaps: [], // ÏµúÍ∑º ÌÉ≠ ÏãúÍ∞Ñ Ï†ÄÏû•
      editingDay: null, // ÌòÑÏû¨ Ìé∏Ïßë Ï§ëÏù∏ ÎÇ†Ïßú (Ïùº)
      currentEditingDate: '', // 'YYMMDD' ÌòïÏãùÏùò ÎÇ†Ïßú Î¨∏ÏûêÏó¥
      formDocId: null, // ÏàòÏ†ï Ïãú ÏÇ¨Ïö©Ìï† Î¨∏ÏÑú ID

      showPasswordModal: false,
      passwordInput: '',
      passwordMessage: '',

      showEditForm: false,
      formEntry: {
        category: 'ÎãπÏßÅ',
        date: '',
        holidayYn: 'N',
        meYn: 'N',
        name: ''
      },
      formMessage: '',
      formError: false,
    };
  },
  computed: {
    currentMonthTitle() {
      const year = this.currentDate.getFullYear();
      const month = this.currentDate.getMonth();
      return `${year}ÎÖÑ ${month + 1}Ïõî`;
    },
    reorderedWeekdays() {
      const days = [...this.weekdays];
      for (let i = 0; i < this.startDayIndex; i++) {
        days.push(days.shift());
      }
      return days;
    },
    daysInCurrentMonth() {
      const year = this.currentDate.getFullYear();
      const month = this.currentDate.getMonth();
      const today = new Date();
      
      const firstDayOfMonth = new Date(year, month, 1).getDay();
      const daysInMonth = new Date(year, month + 1, 0).getDate();
      
      let days = [];
      
      const firstDayWithOffset = (firstDayOfMonth - this.startDayIndex + 7) % 7;
      
      for (let j = 0; j < firstDayWithOffset; j++) {
        days.push({ key: `pad-${j}`, padded: true });
      }
      
      for (let j = 1; j <= daysInMonth; j++) {
        const currentDate = new Date(year, month, j);
        const isToday = currentDate.toDateString() === today.toDateString();
        days.push({ key: `day-${j}`, number: j, padded: false, isToday });
      }
      
      return days;
    }
  },
  watch: {
    startDayIndex(newVal) {
      localStorage.setItem('startDayIndex', newVal);
    }
  },
  created() {
    document.addEventListener('swUpdated', this.showUpdatePrompt, { once: true });
  },
  methods: {
    formatDateToYYMMDD(day) {
      const year = this.currentDate.getFullYear().toString().substring(2);
      const month = (this.currentDate.getMonth() + 1).toString().padStart(2, '0');
      const dayFormatted = day.toString().padStart(2, '0');
      return `${year}${month}${dayFormatted}`;
    },
    
    getDutyName(day) {
      if (!day || !this.dutyData) return null;
      
      const dateKey = this.formatDateToYYMMDD(day);
      
      const dutyPerson = this.dutyData.find(person => 
        // App.vueÏóêÏÑú dateÎ•º String()ÏúºÎ°ú Î≥ÄÌôòÌïòÏó¨ Ï†ÑÎã¨ÌñàÏúºÎØÄÎ°ú Î¨∏ÏûêÏó¥ ÎπÑÍµê
        person.date === dateKey && person.category === 'ÎãπÏßÅ'
      );
      
      // ÎãπÏßÅÏûê Í∞ùÏ≤¥Î•º Î∞òÌôòÌï† Îïå Î¨∏ÏÑú IDÎèÑ Ìï®Íªò Ìè¨Ìï®
      if (dutyPerson) {
          return { 
              name: dutyPerson.name, 
              isMe: dutyPerson.meYn === 'Y', 
              docId: dutyPerson.id,
              fullEntry: dutyPerson // Ï†ÑÏ≤¥ Îç∞Ïù¥ÌÑ∞Î•º Ìè¨Ìï®ÌïòÏó¨ Ìé∏Ïßë Ïãú ÏÇ¨Ïö©
          };
      }
      return null;
    },

    // 3Ìöå ÌÑ∞Ïπò Í∞êÏßÄ Î∞è Ï≤òÎ¶¨
    handleDayClick(day) {
      if (day.padded) return;

      const now = Date.now();
      const dateKey = this.formatDateToYYMMDD(day.number);
      
      this.lastTaps = this.lastTaps.filter(time => now - time < 500); // 0.5Ï¥à Ïù¥ÎÇ¥Ïùò ÌÉ≠Îßå Ïú†ÏßÄ
      this.lastTaps.push(now);

      if (this.lastTaps.length === 3) {
        this.editingDay = day.number;
        this.currentEditingDate = dateKey;
        this.showPasswordModal = true;
        this.passwordInput = '';
        this.passwordMessage = '';
        this.lastTaps = []; // ÌÉ≠ Ï¥àÍ∏∞Ìôî
      }
    },

    // ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏
    async validatePassword() {
      this.passwordMessage = 'ÌôïÏù∏ Ï§ë...';
      const result = await this.checkPassword(this.passwordInput);
      
      if (result.success) {
        this.passwordMessage = '';
        this.showPasswordModal = false;
        this.openEditForm();
      } else {
        this.passwordMessage = result.message || "ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä Ïò¨Î∞îÎ•¥ÏßÄ ÏïäÏäµÎãàÎã§.";
      }
    },
    
    // ÎπÑÎ∞ÄÎ≤àÌò∏ Î™®Îã¨ Îã´Í∏∞
    cancelPassword() {
      this.showPasswordModal = false;
      this.passwordInput = '';
      this.passwordMessage = '';
    },

    // Ìé∏Ïßë Ìèº Ïó¥Í∏∞ Î∞è Îç∞Ïù¥ÌÑ∞ Î°úÎìú
    openEditForm() {
      const dutyEntry = this.getDutyName(this.editingDay);
      
      this.formEntry.date = this.currentEditingDate; // ÎÇ†Ïßú Í≥†Ï†ï
      this.formMessage = '';
      this.formError = false;

      if (dutyEntry && dutyEntry.fullEntry) {
        // Í∏∞Ï°¥ Ìï≠Î™© Î°úÎìú (ÏàòÏ†ï Î™®Îìú)
        const entry = dutyEntry.fullEntry;
        this.formDocId = entry.id;
        this.formEntry.category = entry.category;
        this.formEntry.holidayYn = entry.holidayYn;
        this.formEntry.meYn = entry.meYn;
        this.formEntry.name = entry.name;
      } else {
        // ÏÉà Ìï≠Î™© Ï§ÄÎπÑ (ÏÉùÏÑ± Î™®Îìú)
        this.formDocId = null;
        this.formEntry.category = 'ÎãπÏßÅ'; // Í∏∞Î≥∏Í∞í
        this.formEntry.holidayYn = 'N'; // Í∏∞Î≥∏Í∞í
        this.formEntry.meYn = 'N'; // Í∏∞Î≥∏Í∞í
        this.formEntry.name = ''; // Îπà Í∞í
      }
      this.showEditForm = true;
    },

    // ÎãπÏßÅ Ï†ïÎ≥¥ Ï†ÄÏû•
    async saveDuty() {
      this.formMessage = 'Ï†ÄÏû• Ï§ë...';
      this.formError = false;

      const isSuccess = await this.saveDutyEntry(this.formEntry, this.formDocId);

      if (isSuccess) {
        this.formMessage = this.formDocId ? 'ÏÑ±Í≥µÏ†ÅÏúºÎ°ú ÏàòÏ†ïÎêòÏóàÏäµÎãàÎã§.' : 'ÏÑ±Í≥µÏ†ÅÏúºÎ°ú ÏÉùÏÑ±ÎêòÏóàÏäµÎãàÎã§.';
        setTimeout(this.closeForm, 1500);
      } else {
        this.formMessage = 'Ï†ÄÏû•Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§. ÏΩòÏÜîÏùÑ ÌôïÏù∏ÌïòÏÑ∏Ïöî.';
        this.formError = true;
      }
    },

    // Ìèº Îã´Í∏∞ Î∞è ÏÉÅÌÉú Ï¥àÍ∏∞Ìôî
    closeForm() {
      this.showEditForm = false;
      this.editingDay = null;
      this.currentEditingDate = '';
      this.formDocId = null;
      this.formMessage = '';
      this.formError = false;
      this.formEntry = { category: 'ÎãπÏßÅ', date: '', holidayYn: 'N', meYn: 'N', name: '' };
    },

    prevMonth() {
      this.currentDate = new Date(this.currentDate.getFullYear(), this.currentDate.getMonth() - 1, 1);
      this.closeForm();
    },
    nextMonth() {
      this.currentDate = new Date(this.currentDate.getFullYear(), this.currentDate.getMonth() + 1, 1);
      this.closeForm();
    },
    showUpdatePrompt (event) {
      this.registration = event.detail;
      this.updateExists = true;
    },
    refreshApp () {
      this.updateExists = false;
      if (this.registration && this.registration.waiting) {
        this.registration.waiting.postMessage({ type: 'SKIP_WAITING' });
      }
      window.location.reload();
    }
  }
};
</script>

